import React from 'react'
import { SocialIcons as Icons } from 'Shared/constants/Global'

const SocialIcons = () => {
  const getIcons = () => {
    return Icons
      .map((icon, index) => (
        <span key={icon.key}><a href={icon.link}><i className={icon.className} /></a></span>)
      )
  }

  return <div>{getIcons()}</div>
}

export default SocialIcons



